// Colors
// ---------------------------------

// Default

$color-default-black: #000000;
$color-default-white: #ffffff;
$color-transparent: rgba(255, 255, 255, 0);

// Project palette
// для нейминга цветов используем https://www.htmlcsscolor.com/hex/334482

$color-neon-blue: #2c39f2;
$color-torch-red: #ff1553;
$color-sapphire: #102654;
$color-sapphire-opacity80: rgba(16, 38, 84, 0.8);
$color-sapphire-opacity50: rgba(16, 38, 84, 0.5);
$color-sapphire-opacity30: rgba(16, 38, 84, 0.3);
$color-sapphire-opacity20: rgba(16, 38, 84, 0.2);
$color-egyptian-blue: #103aae;
$color-resolution-blue: #1c3374;
$color-resolution-blue-opacity20: rgba(28, 51, 116, 0.2);
$color-crimson: #d10a42;
$color-crimson-opacity20: rgba(209, 10, 66, 0.2);
$color-ghost-white: #f8f9fc;
$color-torch-red: #ed0233;
$color-torch-red-opacity20: rgba(237, 2, 51, 0.2);
$color-black-russian: #030a1a;
$color-torch-red-error: #ff121f;
$color-grey-opacity20: rgba(120, 120, 120, 0.2);
$color-whisper: #e8e8e8;
$color-white-opacity80: rgba(255, 255, 255, 0.8);
$color-white-opacity70: rgba(255, 255, 255, 0.7);
$color-white-opacity40: rgba(255, 255, 255, 0.4);
$color-white-opacity30: rgba(255, 255, 255, 0.3);
$color-spindle: #b6bdcc;
$color-persian-blue: #2e35ac;
$color-link-water: #c8c9cc;
$color-light-slate-grey: #74819c;

// Gradient

$gradient-hero: linear-gradient(90deg, #1c3477 0%, #1c3477 0.01%, #1a3578 39.11%, #73184b 75.5%, #b4022a 100%);
$gradient-button: linear-gradient(97deg, #ed0233 17.6%, #003eb7 95.31%);
$gradient-card: linear-gradient(135deg, rgba(209, 10, 66, 1) 0%, rgba(0, 53, 159, 1) 100%);
$gradient-advantages: linear-gradient(128deg, #d10a42 0%, #0041ca 45.58%);


// Typography
// ---------------------------------

// переменная используемая в html для подключения скейлинга
$fz-default: 16px;

// у некоторых шрифтов в ios возникают проблемы с символом рубля
// https://www.artlebedev.ru/kovodstvo/sections/159/#13
$ff-placeholder: "Roboto Condensed", "Arial", sans-serif;

// Animation
// ---------------------------------

$tf-default: ease;
$trans-default: 0.3s $tf-default;

// если требуется изинг отличный от $tf-default, то переменную называем не цифрой
$trans-modal: 0.6s cubic-bezier(0.55, 0, 0.1, 1);
$trans-600: 0.6s $tf-default;

// Viewports
// ---------------------------------

$vp-320: 320px;
$vp-768: 768px;
$vp-1366: 1366px;

// Retina
// ---------------------------------

$retina-dpi: 144dpi;
$retina-dppx: 1.5dppx;
